<template>
  <div>
    <v-container fluid class="mt-10">
      <v-card>
        <v-card-title>
          APPLICANT INFORMATION
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="interns"
          :search="search"
          show-expand
        >
         <template v-slot:item.actions="{ item }">
        <v-btn
          icon
          x-small
          @click="delRecord(item)"
          title="Click to delete this applicant"
        >
          <v-icon>mdi-delete</v-icon>9
        </v-btn>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <div class="my-6">
        <intern-details :item="item" />
            
          </div>
        </td>
      </template>
        </v-data-table>
      </v-card>
    </v-container>


    <v-snackbar v-model="snackbar" :color="snackColor" bottom :timeout="5000">
      {{ snackMsg }}
    </v-snackbar>
  </div>
</template>

<script>
import { snackMixin } from "@/mixins";
import { apiClient } from "@/services";
import InternDetails from '../components/InternDetails.vue';
export default {
  name: "UserInfo",
  mixins: [snackMixin],
  components: {
    InternDetails
   
  },
  data() {
    return {
      expanded: [],
      search: "",
      headers: [
        { text: "Application No", value: "application_no" },
        { text: "Surname", value: "surname" },
        { text: "First Name", value: "firstname" },
        { text: "Middle Name", value: "middlename" },
        { text: "Gender", value: "gender" },
        { text: "PhoneNumber", value: "phoneno" },
        { text: "Residential Address", value: "resident_address" },
         { text: "", value: "data-table-expand" },
        { text: "", value: "actions" }
      ],

      interns: [],
    };
  },
   
  async created(){
    this.getRecords()
  },
 
  methods: {  
    async getRecords() {
        this.loading = true;
      try {
        const res = await apiClient.get("/interns?u=2");
        if (Array.isArray(res.data)) {
          this.interns = res.data;
        } else {
          this.displayMsg( res.data, "error");
        }
      } catch (err) {
        this.displayMsg(err, "error");
      }
      this.loading = false;
    },

     delRecord(record) {
      const ok = confirm(
        `Are sure you want to delete ${record.surname} ${record.firstname}?`
      );
      if (ok) {
        this.displayMsg(`Deleting ${record.surname} ${record.firstname}, please wait...`);
        apiClient
          .delete(`/interns?${record.id}`)
          .then((res) => {
            if (!res.data.ok) {
              this.displayMsg(
                `${record.surname} ${record.firstname} not successfully deleted`,
                "error"
              );
              console.log(res.data)
              return;
            }
            this.displayMsg(`${record.surname} ${record.firstname} successfully deleted`);
          })
          .catch((err) => this.displayMsg(`${err.message}`, "error"));
      }
    },

  },
};
</script>
